.main-container {
  width: 100vw;
  float: left;
}
.all-views-conrainer {
  display: flex;
  flex-wrap: nowrap;
}
.header {
  width: 100vw;
  height: 4vh;
  color: #373636;
  background-color: #fff;
  font-family: "Gill Sans", sans-serif;
}
.search-filter {
  width: 15%;
  height: 100%;
  border: 1px solid #ddd;
  padding: 0 2px 0 3px;
}
.search-filter :where(
    .css-dev-only-do-not-override-acm2ia
  ).ant-slider-horizontal.ant-slider-with-marks {
  margin-bottom: 11px;
}
.search-filter :where(.css-dev-only-do-not-override-acm2ia).ant-input-number {
  width: 2vw;
}
.search-filter :where(.css-dev-only-do-not-override-acm2ia).ant-tag {
  padding-inline: 3px;
}
:where(.css-dev-only-do-not-override-acm2ia).ant-btn-primary {
  background-color: gray;
}
.search-result {
  width: 100vw;
  height: 96vh;
  float: left;
}
:root {
  overflow-y: hidden;
  overflow-x: hidden;
}
.list-container {
  float: right;
  left: 75vw;
  width: 25vw;
  height: 96vh;
  display: flex;
  position: absolute;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.center-chart-container {
  width: 75vw;
  height: 96vh;
  display: flex;
  float: left;
}
.topic-tree-container {
  width: calc(100% - 15%);
  height: 100%;
}
.flexlayout__layout {
  top: 7vh !important;
  width: 75vw;
}
.flexlayout__tab {
  overflow: hidden !important;
}
.repo-portrait {
  position: absolute;
  width: 50vw;
  height: 96vh;
  top: 0;
  left: 100vw;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.search-result-id {
  display: flex;
  flex-wrap: nowrap;
}
