.repo-list-title {
  width: 100%;
  height: 3vh;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
  background: #eee;
  color: #6a6969;
}
.repo-list-title p {
  margin: 0;
  font-size: 1.2em;
  font-weight: bold;
  width: 50%;
}
.repo-list-title .result-list-sort {
  display: flex;
  justify-content: center;
  align-items: center;
}
.repo-list-title .result-list-sort .sort-title {
  display: inline-block;
  font-size: 0.4em;
  font-style: italic;
  width: 50px;
}
.repo-list-title .result-list-sort :where(.css-dev-only-do-not-override-1qhpsh8).ant-cascader {
  width: 106px;
  height: 18px;
}
.repo-list-title .result-list-sort .ant-select-selector {
  background: #eee;
}
.repo-list-title .list-rank-title {
  padding-right: 5px;
  font-size: 12px;
}
.repo-list-content {
  width: 100%;
  height: 90vh;
  overflow-y: auto;
  overflow-x: hidden;
}
.list-row-title {
  display: flex;
  justify-content: space-between;
}
.list-row-title .list-repo-name {
  font-size: 20px;
  color: #000;
}
.list-row-title .list-repo-name:hover {
  color: blue;
}
.list-row-title .list-repo-rank {
  font-weight: normal;
}
::-webkit-scrollbar-track {
