.minibar-chart-container {
  width: 100px;
  height: 15px;
  display: flex;
  align-items: center;
}
.minibar-chart-container .minibar-chart {
  width: 90%;
  height: 60%;
  margin-left: 5px;
  position: relative;
}
.minibar-chart-container .minibar-chart .minibar-chart-bg {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f8f6f6;
  border-radius: 4px;
}
.minibar-chart-container .minibar-chart .minibar-chart-target {
  position: absolute;
  left: 0;
  height: 100%;
